        ></textarea>
        <van-uploader v-model="fileList" multiple />
      </div>
      <!--投票选项-->
      <div class="option" v-show="state == 2">
        <div class="option_header">
          <span class="option_header_left">投票选项</span>
          <div class="option_header_right">
            <span class="option_header_right_span">选择距截至时间</span>
            <!-- <van-count-down :time="timeValue" /> -->
            <span class="option_header_right_time" @click="timeClick">{{timeValue}}</span>
          </div>
        </div>
        <div class="addVehicle">
          <div class="add_automobile_item" v-for="(item2,indexs) in addVehicleItem" :key="indexs">
            <div class="add_automobile_item_left">
              <img
                @click="addVehicleChoiceClick(indexs)"
                :src="item2.isShow?select:choice"
